body {
  background-color: #f5f5f5;
}

.App {
  text-align: center;
}

.country-card {
  text-align: left;
  width: 400px;
  margin: 2%;
}

.country-flag {
  width: 100%;
}

.country-flag > img {
  width: 100%;
}

.language-list {
  margin: 3px 0;
}

.country-name {
  text-align: center;
  padding-bottom: 8px;
}

.country-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.regions {
  display: flex;
  justify-content: space-evenly;
}

.region > ul {
  margin: 3px;
}

.search-paper {
  min-width: 50%;
  width:300px;
  margin: 0 auto 20px;
  padding: 10px 0;
}

.search .submit-button {
  margin: 8px;
}

form.search {
  display: flex;
  align-items: flex-end;
  justify-content: center;
}